
.products {
  background: #F7F8FB;
  padding: 30px 0;
}

.product {
  background: #FFF;
  padding: 15px;
  margin-bottom: 15px;
  -webkit-transition: all 200ms ease-in;
  transition: all 200ms ease-in;
  position: relative;
  overflow: hidden;
}
.product-image img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  height: 225px;
}
.product-desc {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 20px 20px 15px;
  background: #FFF;
}
.product-info {
  -webkit-box-flex: 2;
      -ms-flex: 2;
          flex: 2;
}
.product-price {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 20px;
  font-weight: bold;
  color: #51D2B7;
}
.product-price p {
  -webkit-box-flex: 2;
      -ms-flex-positive: 2;
          flex-grow: 2;
  font-size: 20px;
  font-weight: bold;
}
.product-action {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
}
.product-action button {
  border-radius: 0;
  width: 100%;
  display: block;
}
.product:hover {
  -webkit-transform: scale(1.1) translateY(-15px);
          transform: scale(1.1) translateY(-15px);
  -webkit-box-shadow: 7px 9px 18px -2px rgba(61,61,61,1);
          box-shadow: 7px 9px 18px -2px rgba(61,61,61,1);
}
.product-link {
  display: block;
  color: #3D3D3D;
}
.product-link:hover {
  color: #3D3D3D;
  text-decoration: none;
}
.product .product-image {
  width: 100%;
}
.product-desc h4 {
  margin: 3px 0 5px 0;
}
.product-price-cart {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-top: 10px;
}
.product-price-cart p {
  -webkit-box-flex: 2;
      -ms-flex-positive: 2;
          flex-grow: 2;
  font-size: 20px;
  font-weight: bold;
}
.product .product-desc,
.product .product-action {
  -webkit-transition: 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275) -webkit-transform;
  transition: 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275) -webkit-transform;
  transition: 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275) transform;
  transition: 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275) transform, 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275) -webkit-transform;
}
.product:hover .product-desc {
  -webkit-transform: translateY(-40px);
          transform: translateY(-40px);
}
.product:hover .product-action {
  -webkit-transform: none;
          transform: none;
}

.product-details {
  border-bottom: 2px solid #F5F5F5;
  padding: 30px 0;
}
.product-details-desc {
  padding-top: 30px;
}
.product-details-desc h3 {
  margin: 5px 0 30px 0;
}
.product-details-cart {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-top: 30px;
}
.product-details-cart p {
  -webkit-box-flex: 2;
      -ms-flex-positive: 2;
          flex-grow: 2;
  font-size: 20px;
  font-weight: bold;
}
